/* =======================================================================================
~ WYSIWYG - intended to be used on any element whose content is populated by a CMS WYSIWYG
======================================================================================= */

.wysiwyg {
	a:not(.btn) {
		color: $primary;

		&:hover {
			color: $primary;
			text-decoration: underline;
		}
	}

	ul:not(.gform_fields):not(.gfield_radio):not(.gfield_checkbox),
	ol {
		list-style: none;
		padding-left: 2rem;

		ul, ol {
			margin-bottom: 0;
		}
	}
	ol {
		list-style: decimal outside;
	}
	ul:not(.gform_fields):not(.gfield_radio):not(.gfield_checkbox) {
		list-style: disc outside;
	}

	h1, h2, h3, h4, h5, h6 { font-weight: bold; }
	h1 { font-size: $font-size-base * 2 }
	h2 { font-size: $font-size-base * 2.25 }
	h3 { font-size: $font-size-base * 2 }
	h4 { font-size: $font-size-base * 1.75 }
	h5 { font-size: $font-size-base * 1.5 }
	h5 { font-size: $font-size-base * 1.25 }
	h6 { font-size: $font-size-base }

    blockquote {
    	border-left: 4px solid $gray-lighter;
    	padding-left: 1.5rem;
    }

    hr {}
    
    img, figure {
    	max-width: 100%;
    }

    .wp-caption {
    	img {
    		width: 100%;
    		height: auto;
    	}

		.wp-caption-text {
			margin-top: 1rem;
			font-size: .85em;
			font-style: italic;
		}
	}

	> * {
		margin-bottom: 2rem;

		&:last-child {
			margin-bottom: 0;
		}
	}
}

// Extra small only
@include media-breakpoint-only(xs) {
	.composition {
	    > img,
	    > .wp-caption {
	    	width: 100% !important;
	    	height: auto !important;
	    }
	}
}


